import PostsComponent from "./postsComponent";
import CarouselComponent from './carouselComponent';
import loader from './logo.svg';
import AlertComponent from "./alertComponent";
import useFetch from "./useFetch";


const HomeComponent = () => {
    const { data: posts, error, isPending } = useFetch("http://localhost:8000/posts")

    // for the record, this right here is really smart
    // const deletePost = (id) => {
    //     const newPost = posts.filter(
    //         post => post.id !== id
    //     );

    //     setPost(newPost);
    // }
    return (
        <div>
            <CarouselComponent />
            {error && <AlertComponent error={error} />}
            {isPending && <div><img src={loader} className="App-logo" width="200" alt="preloader" /> <span>Loading...</span></div>}
            {posts && <PostsComponent posts={posts} />}
        </div>
    );
}

export default HomeComponent;