import { useReducer, useState } from 'react';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';

const ReducerComponent = () => {
    const initialState = 0;
    const reducer = (state, action) => {
        switch(action.type){
        case "increment":
            return state + 1;
        case "decrement":
            return state - 1;

        default:
            throw new Error();
        }
    }
    const [state, dispatch] = useReducer( reducer, initialState);
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');

    const initialStage = [
        {id: Date.now(), name: "Adam", email: "adam@shitjob.com" }
    ]
    const meducer = (stage, actor) => {
        switch(actor.type){
            case "add":
                return [...stage, actor.body]
            
            default:
                throw new Error();
        }
    }
    const [stage, distro] = useReducer(meducer, initialStage);

    const addContact = (e) => {
        e.preventDefault();
        const contact = {
            id: Date.now(),
            name: name,
            email: email
        }

        setName('');
        setEmail('');
        distro({ type: "add", body: contact})
    }
    
    return ( 
        <div className="container">

            <h1>Hooks Explained</h1>
            <p>{state}</p>

            <Button onClick={()=>{
                dispatch(
                    {
                        type: "decrement"
                    }
                )
            }}>Decrease</Button>
            <Button onClick={()=>{
                dispatch(
                    {
                        type: "increment"
                    }
                )
            }}>Increase</Button>

<Card className='p-5 m-3'>
            <form onSubmit={addContact}>
                <input type="text" value={name} onChange={
                    (e)=>{
                        setName(e.target.value)
                    }
                } className="form-control m-3" placeholder='Enter Your Name'/>
                <input type="text" value={email} onChange={
                    (e)=>{
                        setEmail(e.target.value)
                    }
                } className="form-control m-3" placeholder='Enter Your email' />
                <Button type="Submit" className='m-3'> Add New Contact</Button>
            </form>
            </Card>

            <div>
                {
                    stage.map(
                        contact =>{
                            return (
                                <li key={contact.id}>
                                    <p>{contact.name}</p>
                                </li>
                            )
                        }
                    )
                }
            </div>
        </div>
     );
}
 
export default ReducerComponent;
