import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';
import { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { Spinner } from 'react-bootstrap';



const CreatePostComponent = () => {
    const [isPending, setIsPending] = useState(true)
    const [title, setTitle] = useState('');
    const [author, setAuthor] = useState('');
    const [body, setBody] = useState('');
    const post = { title, author, body };

    const history = useHistory()
    //post blog function
    const handleSubmit = (e) => {
        e.preventDefault();
        setIsPending(false);
        //timeout before submitting, so Spinner can show
        setTimeout(
            () => {
                const url = 'http://localhost:8000/posts';
                fetch(
                    url, {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify(post)
                }
                ).then(
                    res => {
                        res.json();
                        history.push('/')
                    }
                ).then(

                );
            }, 3000
        );

    }
    return (
        <Row className='pt-5'>
            <Col md={{ span: 4, offset: 4 }} sm={12}>
                <Card className='p-5'>
                    <Form onSubmit={handleSubmit}>
                        <Form.Group className="mb-3" controlId="formBasicEmail">
                            <Form.Label>Title</Form.Label>
                            <Form.Control type="text" placeholder="Enter blog title" value={title}
                                onChange={(e) => {
                                    return setTitle(e.target.value)
                                }
                                } />
                            {/* For later maybe
                        <Form.Text className="text-muted">
                            We'll never share your email with anyone else.
                        </Form.Text> */}
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formBasicEmail">
                            <Form.Label>Author</Form.Label>
                            <Form.Control type="text" placeholder="Enter your name" value={author}
                                onChange={(e) => {
                                    return setAuthor(e.target.value)
                                }
                                } />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formBasicStory">
                            <Form.Label>Your Story</Form.Label>
                            <Form.Control as="textarea" rows={3} value={body}
                                onChange={(e) => {
                                    return setBody(e.target.value)
                                }
                                } />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="formBasicCheckbox">
                            <Form.Check type="checkbox" label="Remember Me" />
                        </Form.Group>

                        {isPending && <Button variant="primary" type="submit">
                            Submit
                        </Button>}
                        {!isPending && <Button variant="primary" type="submit">
                            Submitting your story <Spinner
                                as="span"
                                animation="grow"
                                size="sm"
                                role="status"
                                aria-hidden="true"
                            />
                        </Button>}
                    </Form>
                </Card>
            </Col>
        </Row>
    );
}


export default CreatePostComponent;