import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import { useParams } from 'react-router-dom';
import useFetch from './useFetch';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import AlertComponent from './alertComponent';
import loader from './logo.svg';

const PostsDetailsComponent = () => {
    const { id } = useParams();
    const { data: post, error, isPending } = useFetch(`http://localhost:8000/posts/${id}`);

    return (
        <Row>
            <Col className='p-5'>
                {post && <Card>
                    <Card.Header as="h5">{post.title}</Card.Header>
                    <Card.Body>
                        <Card.Title>
                            <small>{post.author}</small>
                        </Card.Title>
                        <Card.Text>
                            {post.body}
                        </Card.Text>
                        <Button variant="primary">Oh</Button>
                    </Card.Body>
                </Card>}
                {error && <AlertComponent error={error} />}
                {isPending && <div><img src={loader} className="App-logo" width="200" alt="preloader" /> <span>Loading...</span></div>}

            </Col>
        </Row>
    );
}

export default PostsDetailsComponent;
