// import logo from './logo.svg';
import NavbarComponent from './navbarComponent';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import PostsDetailsComponent  from './postsDetailsComponent';
import CreatePostComponent from './createPostComponent';
import HomeComponent from './homeComponent';
import FooterComponent from './footerComponent';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Error404 from './error404';
import { useContext } from 'react';
import { ThemeContext } from '.';
import Button from 'react-bootstrap/Button';
import ReducerComponent from './reducerComponent';


function App() {
  const [ theme, toggleTheme ] = useContext(ThemeContext);
  return (
    <div className="App" style={{ background: theme.background, color: theme.foreground}}>
      <Router>
        <header>
          <NavbarComponent />
          <Button variant='outline-secondary' onClick={toggleTheme}>Toggle Dark Mode</Button>
        </header>
        <Switch>
          <Route exact path="/">
            <HomeComponent />
          </Route>
       
          <Route exact path="/create">
            <CreatePostComponent />
          </Route>
       
          <Route exact path="/posts/:id">
            <PostsDetailsComponent />
          </Route>
          
          <Route exact path="/count">
            <ReducerComponent />
          </Route>
          
          <Route path="*">
            <Error404 />
          </Route>
        </Switch>
        <FooterComponent />
      </Router>
    </div>
  );
}

export default App;
